import { CurrentInteraction, LoadingResponseSkeleton, UserInput } from "@rp/react/web/chat"
import { Box } from "@rp/react/web/mui/core"

type ChatPaneLoadingChatProps = {
  currentInteraction: CurrentInteraction
  userInitials: string
}
function ChatPaneLoadingChat({ currentInteraction, userInitials }: ChatPaneLoadingChatProps) {
  return (
    <Box
      sx={{
        width: "100%",
        maxWidth: 700,
        mx: "auto",
      }}
    >
      <UserInput variant="mobile" userInitials={userInitials} disableScroll>
        {currentInteraction.displayedMessage ?? currentInteraction.userInput}
      </UserInput>
      <LoadingResponseSkeleton variant="mobile" />
    </Box>
  )
}

export { ChatPaneLoadingChat }
